import React,{Component} from 'react';
import styled from 'styled-components';
import './Boxh.css';


const Styles = styled.div`

`;

class Boxh extends Component {
    constructor(props){
        super(props)
        this.state = {
          a : 2,
        };
       
    }

   
    render(){
        return (
            <Styles>
                <div style={{backgroundColor:"#FFFCFC",paddingTop:"15px"}}>
               
                
                
                    <h1 className="hh" style={{paddingTop:"3%",paddingBottom:"0%",color:"#163948"}}><strong>Conception Checklist Tool</strong></h1>
                    
                    <h5 className="para1" style={{paddingLeft:"15%",paddingRight:"15%",paddingTop:"3%",paddingBottom:"1.5%"}}>Whether you have already decided to start a family or still in the process of considering starting one,</h5>
                    <h5 className="para2" style={{paddingLeft:"12%",paddingRight:"12%",paddingBottom:"3%"}}> this tool will help you keep track of the day-to-day factors that impact your fertility and chances of having a healthy baby.</h5>
                
                    </div>
                
                

            </Styles>
            

        )
    }


}



export default Boxh;